/* footer */
.site-footer {
  position: relative;
  max-width: 125rem;
  width: 100%;
  margin: auto;
  padding: 1.7rem 0;
  color: $gray;
  background-position: 20% 70%;
  background-repeat: no-repeat;
  background-size: cover;
  border-left: solid 0.1rem rgba($primary-color, 0.2);
  border-right: solid 0.1rem rgba($primary-color, 0.2);
  border-bottom: solid 0.1rem rgba($primary-color, 0.2);
  z-index: 5;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $bg-color;
    opacity: 0.65;
    z-index: 1;
  }

  .text-holder {
    font-size: 1.4rem;
  }

  .footer-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 1rem;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .container-fluid {
    position: relative;
    z-index: 1;
  }

  .h3,
  .light-text {
    color: darken($mercury, 20);
  }

  .counter-holder {
    opacity: 0.1;
    transition: $transition;

    &:hover {
      opacity: 1;
    }
  }

  .text-holder {
    @include media-breakpoint-down(lg) {
      margin-bottom: 4rem;
    }
  }

  .footer-menu {
    margin: 0;
    padding: 0;
    list-style: none;
    font-size: 1.5rem;

    @include media-breakpoint-down(md) {
      margin-bottom: 4rem;
    }

    .menu-item {
      display: flex;

      .bi {
        margin-right: 0.3rem;
      }

      &:not(:last-child) {
        margin-bottom: 1rem;
      }
    }

    .menu-link {
      text-decoration: none;

      &:hover {
        color: darken($mercury, 20);
      }
    }
  }

  .social-links {
    justify-content: flex-start;

    @include media-breakpoint-down(md) {
      margin-bottom: 4rem;
    }

    li {
      &:not(:last-child) {
        margin-right: 1.5rem;
      }
    }

    a {
      padding: 0;
    }

    .icon-holder {
      width: 2rem;
    }
  }

  .counter-holder {
    display: inline-flex;
    opacity: 0.2;
    transition: all 0.3s;

    @include media-breakpoint-down(md) {
      margin-bottom: 1rem;
    }

    &:hover {
      opacity: 1;
    }
  }
}
