.widget_wpforo_recent_posts {
  .wpforo-widget-wrap {
    .posttext {
      display: none;
    }

    li {
      .wpforo-list-item-right {
        width: unset !important;
        float: unset !important;
      }

      &:last-child {
        border: none !important;
      }
    }

    .posttitle {
      color: $mercury;

      a {
        &:hover {
          text-decoration: underline !important;
        }
      }
    }

    .postuser {
      color: #9b9a97;
    }
  }

  &.widget {
    .wpforo-list-item-left {
      display: none;
    }

    .widget-title {
      border-bottom: 0.1rem solid #202020;
    }

    .wpforo-widget-wrap {
      .wpforo-widget-content {
        padding: 0 0 1.2rem 0 !important;
      }
    }
  }
}

.wp-block-quote {
  margin-bottom: 2rem;
}
